.SideNav {
  height: 100%;
  max-height: 100%;
  overflow: auto;
  width: $menu-width;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  background-color: $osac-sidebar-color;
  transform: translateX(-$menu-width);
  opacity: 1;
  padding-top: 0px;
  transition: all 0.5s cubic-bezier(0.7,.01,.25,.8);

  &__Menu {
    &__Header {
      font-family: "lobster";
      color: $osac-color;
      font-size: 33px;
      padding: 10px 0 5px 20px;
    }
  }

  &__Link {
    padding: 10px 10px 10px 20px;
    text-decoration: none;
    font-size: 18px;
    font-weight: 500;
    display: block;
    color: #686f7a;
    transition: 0.05s;
    &:hover {
      text-decoration: none;
      background: #e2e3e4;
      color: $osac-color;
    }
  }
}

#sidebar_overlay {
  z-index: 2;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
}